import { generateRSAKeys, arrayBufferToPem, pemToArrayBuffer, aesEncrypt, aesDecrypt } from './crypto';

export class WebSocketClient {
    private url: string;
    private socket: WebSocket | null = null;
    private isConnected: boolean = false;
    private rsaPublic: CryptoKey | null = null;
    private rsaPrivate: CryptoKey | null = null;
    private rsaPublicServer: CryptoKey | null = null;
    private aesKey: CryptoKey | null = null;
    private aesServerKey: CryptoKey | null = null;
    private messageHandlers: Map<string, (data: any) => void> = new Map();
    private connectionPromise: Promise<void> | null = null;
    private authPromise: Promise<void> | null = null;
    private requestTimeout: number = 10000; // 10 секунд таймаут

    constructor(url: string) {
        this.url = url;
    }

    private async initEncryption(): Promise<void> {
        // Генерируем RSA ключи
        const { publicKey, privateKey } = await generateRSAKeys();
        this.rsaPublic = publicKey;
        this.rsaPrivate = privateKey;

        // Отправляем публичный ключ серверу
        const publicKeyPem = await arrayBufferToPem(this.rsaPublic, 'PUBLIC KEY');
        await this.sendRaw({
            type: 'key_exchange',
            action: 'client_key',
            key: publicKeyPem
        });
    }

    private async sendRaw(data: any): Promise<void> {
        if (!this.socket) {
            throw new Error('WebSocket not connected');
        }
        this.socket.send(JSON.stringify(data));
    }

    public async connect(): Promise<void> {
        if (this.connectionPromise) {
            return this.connectionPromise;
        }

        this.connectionPromise = new Promise((resolve, reject) => {
            try {
                this.socket = new WebSocket(this.url);

                const timeout = setTimeout(() => {
                    if (!this.isConnected) {
                        this.close();
                        reject(new Error('Connection timeout'));
                    }
                }, this.requestTimeout);

                this.socket.onopen = async () => {
                    console.log('WebSocket connected');
                    this.isConnected = true;
                    clearTimeout(timeout);
                    
                    try {
                        await this.initEncryption();
                        resolve();
                    } catch (error) {
                        console.error('Encryption initialization failed:', error);
                        reject(error);
                    }
                };

                this.socket.onmessage = async (event) => {
                    try {
                        let data = JSON.parse(event.data);
                        
                        // Обработка обмена ключами
                        if (data.type === 'key_exchange') {
                            if (data.action === 'server_key' && data.key) {
                                this.rsaPublicServer = await pemToArrayBuffer(data.key);
                            }
                            return;
                        }

                        // Расшифровываем сообщение если есть ключи
                        if (this.aesKey && data.encrypted) {
                            const decrypted = await aesDecrypt(new Uint8Array(data.data), this.aesKey);
                            data = JSON.parse(decrypted);
                        }

                        const handler = this.messageHandlers.get(`${data.type}:${data.action}`);
                        if (handler) {
                            handler(data);
                        }
                    } catch (error) {
                        console.error('Error processing message:', error);
                    }
                };

                this.socket.onerror = (error) => {
                    console.error('WebSocket error:', error);
                    this.isConnected = false;
                    clearTimeout(timeout);
                    reject(error);
                };

                this.socket.onclose = () => {
                    console.log('WebSocket closed');
                    this.isConnected = false;
                    this.connectionPromise = null;
                    clearTimeout(timeout);
                };
            } catch (error) {
                reject(error);
            }
        });

        return this.connectionPromise;
    }

    public async send(message: any): Promise<void> {
        if (!this.isConnected || !this.socket) {
            await this.connect();
        }

        const timeout = setTimeout(() => {
            throw new Error('Send timeout');
        }, this.requestTimeout);

        try {
            let data = message;
            
            // Шифруем сообщение если есть ключи
            if (this.aesKey && message.type !== 'key_exchange') {
                const encrypted = await aesEncrypt(JSON.stringify(message), this.aesKey);
                data = {
                    encrypted: true,
                    data: Array.from(encrypted)
                };
            }

            await this.sendRaw(data);
            clearTimeout(timeout);
        } catch (error) {
            clearTimeout(timeout);
            throw error;
        }
    }

    public on(type: string, action: string, handler: (data: any) => void): void {
        this.messageHandlers.set(`${type}:${action}`, handler);
    }

    public off(type: string, action: string): void {
        this.messageHandlers.delete(`${type}:${action}`);
    }

    public close(): void {
        if (this.socket) {
            this.socket.close();
            this.socket = null;
            this.isConnected = false;
            this.connectionPromise = null;
        }
    }
} 