// Шифрование AES
export async function aesEncrypt(data: string, key: CryptoKey): Promise<Uint8Array> {
    const encoder = new TextEncoder();
    const dataBuffer = encoder.encode(data);
    
    const iv = crypto.getRandomValues(new Uint8Array(12));
    const encryptedData = await crypto.subtle.encrypt(
        {
            name: "AES-GCM",
            iv: iv
        },
        key,
        dataBuffer
    );
    
    // Объединяем IV и зашифрованные данные
    const result = new Uint8Array(iv.length + encryptedData.byteLength);
    result.set(iv);
    result.set(new Uint8Array(encryptedData), iv.length);
    
    return result;
}

// Расшифровка AES
export async function aesDecrypt(encryptedData: Uint8Array, key: CryptoKey): Promise<string> {
    const iv = encryptedData.slice(0, 12);
    const data = encryptedData.slice(12);
    
    const decryptedBuffer = await crypto.subtle.decrypt(
        {
            name: "AES-GCM",
            iv: iv
        },
        key,
        data
    );
    
    const decoder = new TextDecoder();
    return decoder.decode(decryptedBuffer);
}

// Генерация RSA ключей
export async function generateRSAKeys(): Promise<CryptoKeyPair> {
    return await crypto.subtle.generateKey(
        {
            name: "RSA-OAEP",
            modulusLength: 2048,
            publicExponent: new Uint8Array([1, 0, 1]),
            hash: "SHA-256"
        },
        true,
        ["encrypt", "decrypt"]
    );
}

// Преобразование ключа в PEM формат
export async function arrayBufferToPem(key: CryptoKey, type: string): Promise<string> {
    const exported = await crypto.subtle.exportKey("spki", key);
    const exportedAsString = String.fromCharCode.apply(null, new Uint8Array(exported) as any);
    const exportedAsBase64 = btoa(exportedAsString);
    return `-----BEGIN ${type}-----\n${exportedAsBase64}\n-----END ${type}-----`;
}

// Преобразование PEM в ключ
export async function pemToArrayBuffer(pem: string): Promise<CryptoKey> {
    const pemHeader = "-----BEGIN PUBLIC KEY-----";
    const pemFooter = "-----END PUBLIC KEY-----";
    const pemContents = pem.substring(
        pemHeader.length,
        pem.length - pemFooter.length
    );
    const binaryDer = atob(pemContents);
    const arrayBuffer = new Uint8Array(binaryDer.length);
    for (let i = 0; i < binaryDer.length; i++) {
        arrayBuffer[i] = binaryDer.charCodeAt(i);
    }
    return await crypto.subtle.importKey(
        "spki",
        arrayBuffer,
        {
            name: "RSA-OAEP",
            hash: "SHA-256"
        },
        true,
        ["encrypt"]
    );
} 